
body {
  background-color: whitesmoke; }

.content, .content-large {
  margin: 0 auto;
  width: 1366px;

  &-large {
    width: 1666px; }

  &-full {
    width: 100%; } }

.progress-bars {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%; }

.progress {
  width: 10em;
  margin: 1em;

  h4 {
    font-size: 2em;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    color: #6d6d6d; } }

.project {
  &-item {
    font-size: 1.2em;
    line-height: 2em;
    font-family: 'Montserrat', sans-serif;
    padding: 1em;
    height: 20em;
    color: white; }
  &-column {
    width: 50%;
    float: left;
    position: relative;
    height: 100%;
    nackground: blue; }

  &-text {

    width: 700px;
    font-size: 1.2em;
    color: white;
    text-align: left !important; }

  &-cell {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-50%);
    text-align: center; }

  &-name {
    h2 {
      margin-top: 0; } }
  &-image {
    img {
      width: 25em; } } }

.page-group {
  width: 65em;
  margin: 0 auto;
  margin-top: 1.5em;
  margin-bottom: 1em;

  &-title {
    padding-left: 1.5em; } }

@media screen and (max-width: 1366px) {
  .content, .content-large, .page-group {
    width: 100%; }
  .header-text {
    font-size: 1.2em; }
  .project-text {
    width: 300px;
    font-size: .9em;
    line-height: 1.8em; }
  .project-image img {
    width: 16em; }
  .progress {
    width: 8em; } }
